<template>
  <div class="content">
    <base-header class="pb-6" type="nutrients">
      <b-row align-v="center" class="py-4">
        <b-col>
          <h6 class="h2 text-white d-inline-block mb-0 mr-5" v-show='field_name'>Nutrients - {{ field_name }} - {{ model.node_address }} </h6>
          <div v-show='loading' class='mab_spinner light right'></div>
        </b-col>
      </b-row>
    </base-header>

    <div class='container-fluid mt--6'>

      <!-- Browse Nutrient Templates -->
      <b-modal v-model="browseTemplatesModalVisible" centered no-close-on-esc no-close-on-backdrop size='lg'>
        <template #modal-header="{ close }">
          <h6 class="modal-title">Nutrient Templates</h6>
        </template>

        <template #default="{ hide }">

          <b-row>
            <b-col>
              <b-form-select v-model="perPage">
                <b-form-select-option :value="5">5</b-form-select-option>
                <b-form-select-option :value="10" selected>10</b-form-select-option>
                <b-form-select-option :value="25">25</b-form-select-option>
                <b-form-select-option :value="50">50</b-form-select-option>
              </b-form-select>
            </b-col>
            <b-col md></b-col>
            <b-col> 
              <base-input v-model="filterText"
                prepend-icon="fas fa-search"
                placeholder="Search...">
              </base-input>
            </b-col>
          </b-row>

          <b-row>
            <b-col md>
              <b-table
                striped
                bordered
                outlined
                small
                stacked="md"
                responsive
                show-empty
                primary-key="id"
                :per-page="perPage"
                :current-page="currentPage"
                :filter="filterText"
                :fields="templateTableColumns"
                :items="loadedTemplates"
                :busy.sync="loading">

                <template #cell()="data">{{ data.value }}</template>

                <template #cell(actions)="data">
                  <div class="d-flex justify-content-center">
                    <b-button
                      :disabled="!userCan('Edit', 'Nutrient Templates', data.item.id, 'O') || data.item.name == 'Default Template' || loading"
                      @click="applyNutrientTemplate(data.index, data.item)"
                      class="btn"
                      variant="outline-primary"
                      size="sm"
                      icon>
                    Apply
                    </b-button>
                    <b-button
                      :disabled="!userCan('Edit', 'Nutrient Templates', data.item.id, 'O') || data.item.name == 'Default Template' || loading"
                      @click="editNutrientTemplate(data.index, data.item)"
                      class="btn"
                      variant="outline-primary"
                      size="sm"
                      icon>
                    Edit
                    </b-button>
                    <b-button
                      :disabled="!userCan('Delete', 'Nutrient Templates', data.item.id, 'O') || data.item.name == 'Default Template' || loading"
                      @click="removeNutrientTemplate(data.index, data.item)"
                      class="btn"
                      variant="outline-primary"
                      size="sm"
                      icon>
                    Remove
                    </b-button>
                  </div>
                </template>

                <template #table-busy>
                  <div class="text-center">
                    <div class='mab_spinner'></div>
                  </div>
                </template>

                <template #empty="scope">
                  <div class="text-center">
                    No templates found
                  </div>
                </template>

              </b-table>
            </b-col>
          </b-row>

          <b-row>
            <b-col md>
              Showing {{ Math.min(1 + (perPage * (currentPage-1)), totalRows) }} to {{ Math.min((perPage*(currentPage-1)) + perPage, totalRows)  }} of {{ totalRows }} templates
            </b-col>
            <b-col md>
              <b-pagination v-model="currentPage" :total-rows="totalRows" :per-page="perPage" align="right"></b-pagination>
            </b-col>
          </b-row>

        </template>
        <template #modal-footer="{ ok, cancel, hide }">
          <b-button
            :disabled="!userCan('Add', 'Nutrient Templates') || loading"
            variant="outline-primary"
            class="ml-auto"
            @click="addNutrientTemplate">
            Add Template
          </b-button>
          <b-button variant="outline-primary" @click="closeBrowseTemplatesModal">
            Close
          </b-button>
        </template>
      </b-modal>

      <!-- Manage Nutrient Template Modal -->
      <b-modal v-model="manageTemplateModalVisible" centered no-close-on-esc no-close-on-backdrop size='lg'>
        <template #modal-header="{ close }">
          <h6 class="modal-title">{{ currentMode }} Template</h6>
        </template>

        <template #default="{ hide }">
          <validation-observer ref='managetpl' slim>
            <b-row>
              <b-col md>
                <base-input label="Template Name" v-model="template_name" placeholder="Template Name" name="template name" rules="required"></base-input>
              </b-col>
            </b-row>

            <b-row>
              <b-col md>
                <base-input label="Polynomial 1" v-model="template_model.poly1" placeholder="Poly1" name="poly1" rules="required|double"></base-input>
              </b-col>
              <b-col md> 
                <base-input label="Polynomial 2" v-model="template_model.poly2" placeholder="Poly2" name="poly2" rules="required|double"></base-input>
              </b-col>
            </b-row>

            <b-row>
              <b-col md>
                <base-input label="Lower Limit" v-model="template_model.lower_limit" placeholder="Lower Limit" name="lower limit" rules="required"></base-input>
              </b-col>
              <b-col md> 
                <base-input label="Upper Limit" v-model="template_model.upper_limit" placeholder="Upper Limit" name="upper limit" rules="required"></base-input>
              </b-col>
            </b-row>

            <b-row>
              <b-col md>
                <base-input label="Soil Type" vid="soil_type" rules="required" name="soil type">
                  <el-select placeholder="Soil Type" v-model="template_model.soil_type">
                    <el-option v-for="item in soilTypes" :key="item.value" :label="item.label" :value="item.value"></el-option>
                  </el-select>
                </base-input>
              </b-col>
              <b-col md>
                <base-input label="Metric" vid="metric" rules="required" name="metric">
                  <el-select placeholder="Metric" v-model="template_model.metric">
                    <el-option v-for="item in metrics" :key="item.value" :label="item.label" :value="item.value"></el-option>
                  </el-select>
                </base-input>
              </b-col>
              <b-col md> 
                <base-input label="Crop Name" v-model="template_model.crop_name" placeholder="Crop Name" name="crop name" rules="required"></base-input>
              </b-col>
            </b-row>

          </validation-observer>
        </template>

        <template #modal-footer="{ ok, cancel, hide }">
          <b-button variant="outline-primary" class="ml-auto" @click="closeManageNutrientTemplateModal">
            Close
          </b-button>
          <b-button
            variant="outline-primary"
            @click="saveNutrientTemplate">
            {{ currentMode == 'Edit' ? 'Update' : 'Add' }}
          </b-button>
        </template>
      </b-modal>

      <div class='row'>
        <div class='col-md-12'>
          <card body-classes="px-0 py-0">

            <template slot="header">
              <b-row>
                <b-col>
                  <base-button 
                    :disabled="!userCan('Graph', 'Nutrients', model.node_address, 'O') || loading"
                    @click.native="goToGraph()"
                    class="btn"
                    type="primary"
                    size="sm"
                    v-b-tooltip.hover.top title="Navigate to node's graphing screen."
                    icon>
                    Graph
                  </base-button>
                </b-col>
              </b-row>
            </template>

            <div class="card-body">

              <b-row>
                <b-col md>
                  <card body-classes="px-1 py-1">
                    <div class="card-body">
                      <b-row>
                        <b-col md align-self="center">
                          <b-toast :visible="true" title="Readings Date" static no-auto-hide no-close-button>
                            {{ latestDateReported }}
                          </b-toast>
                        </b-col>
                        <b-col md align-self="center">
                          <b-toast :visible="true" title="Active Template" static no-auto-hide no-close-button>
                            {{ loadedTemplateName }}
                          </b-toast>
                        </b-col>
                        <b-col md align-self="center">
                          <base-button
                            :disabled="!userCan('View', 'Nutrient Templates') || loading"
                            @click.native="openNutrientTemplatesModal()"
                            class="btn"
                            type="primary"
                            icon>
                            Nutrient Templates
                          </base-button>
                        </b-col>
                        <b-col md align-self="center">
                          <b-toast :visible="true" title="Install Depth" static no-auto-hide no-close-button>
                            <validation-observer ref='nform' slim>
                              <base-input vid="install_depth" rules="required" name="install depth">
                                <el-select @change="syncFields" size="small" placeholder="Install Depth" v-model="model.install_depth">
                                  <el-option v-for="item in install_depths" :key="item.value" :label="item.label" :value="item.value"></el-option>
                                </el-select>
                              </base-input>
                            </validation-observer>
                          </b-toast>
                        </b-col>
                      </b-row>
                    </div>
                  </card>
                </b-col>
              </b-row>

              <b-row>
                <b-col md>

                  <card body-classes="px-1 py-1">
                    <template slot="header">
                      <strong>Raw</strong>
                    </template>
                    <div class="card-body">
                      <b-row>
                        <b-col>
                          <b-toast :visible="true" title="M3.1" static no-auto-hide no-close-button>
                            {{ raw.M3_1 }}
                          </b-toast>
                        </b-col>

                        <b-col>
                          <b-toast :visible="true" title="M4.1" static no-auto-hide no-close-button>
                            {{ raw.M4_1 }}
                          </b-toast>
                        </b-col>

                        <b-col>
                          <b-toast :visible="true" title="M5.1" static no-auto-hide no-close-button>
                            {{ raw.M5_1 }}
                          </b-toast>
                        </b-col>

                        <b-col>
                          <b-toast :visible="true" title="M6.1" static no-auto-hide no-close-button>
                            {{ raw.M6_1 }}
                          </b-toast>
                        </b-col>

                        <b-col>
                          <b-toast :visible="true" title="Avg." static no-auto-hide no-close-button>
                            {{ raw.avg }}
                          </b-toast>
                        </b-col>
                      </b-row>
                    </div>
                  </card>

                  <card body-classes="px-1 py-1">
                    <template slot="header">
                      <strong>PPM</strong>
                    </template>
                    <div class="card-body">
                      <b-row>
                        <b-col>
                          <b-toast :visible="true" title="M3.1" static no-auto-hide no-close-button>
                            {{ ppm.M3_1 }}
                          </b-toast>
                        </b-col>

                        <b-col>
                          <b-toast :visible="true" title="M4.1" static no-auto-hide no-close-button>
                            {{ ppm.M4_1 }}
                          </b-toast>
                        </b-col>

                        <b-col>
                          <b-toast :visible="true" title="M5.1" static no-auto-hide no-close-button>
                            {{ ppm.M5_1 }}
                          </b-toast>
                        </b-col>

                        <b-col>
                          <b-toast :visible="true" title="M6.1" static no-auto-hide no-close-button>
                            {{ ppm.M6_1 }}
                          </b-toast>
                        </b-col>

                        <b-col>
                          <b-toast :visible="true" title="Avg." static no-auto-hide no-close-button>
                            {{ ppm.avg }}
                          </b-toast>
                        </b-col>
                      </b-row>
                    </div>
                  </card>

                  <card body-classes="px-1 py-1">
                    <template slot="header">
                      <strong>Pounds</strong>
                    </template>
                    <div class="card-body">
                      <b-row>
                        <b-col>
                          <b-toast :visible="true" title="M3.1" static no-auto-hide no-close-button>
                            {{ pounds.M3_1 }}
                          </b-toast>
                        </b-col>

                        <b-col>
                          <b-toast :visible="true" title="M4.1" static no-auto-hide no-close-button>
                            {{ pounds.M4_1 }}
                          </b-toast>
                        </b-col>

                        <b-col>
                          <b-toast :visible="true" title="M5.1" static no-auto-hide no-close-button>
                            {{ pounds.M5_1 }}
                          </b-toast>
                        </b-col>

                        <b-col>
                          <b-toast :visible="true" title="M6.1" static no-auto-hide no-close-button>
                            {{ pounds.M6_1 }}
                          </b-toast>
                        </b-col>

                        <b-col>
                          <b-toast :visible="true" title="Avg." static no-auto-hide no-close-button>
                            {{ pounds.avg }}
                          </b-toast>
                        </b-col>
                      </b-row>
                    </div>
                  </card>
                </b-col>

                <b-col md>

                  <card>
                    <template slot="header">
                      <strong>Nutrient Gauge</strong>
                    </template>
                    <div class="card-body">
                      <b-row>
                        <b-col md>
                          <NiftyGauge
                            :angle="gauge_value"
                            :size="'50%'"
                            :subl="lower_limit"
                            :subm="ppm.avg + ' (PPM)'"
                            :subr="upper_limit"
                            :label="nutrient_label"
                            >
                          </NiftyGauge>
                        </b-col>
                      </b-row>
                    </div>
                  </card>
                </b-col>
              </b-row>
            </div>
          </card>

          <card body-classes="px-0 py-0">

            <template slot="header">
              <h3 class="mb-0">Graph Configuration</h3>
            </template>

            <div class="card-body">
              <validation-observer ref='nform' slim>
                <b-row align-v="center">
                  <b-col md>
                    <base-input label="Graph Type" name="graph type" rules="required" vid="graph_type">
                      <el-select @change="syncFields" v-model="model.graph_type" filterable placeholder="Graph Type">
                        <el-option label="Nutrients (RAW)" value='nutrient'></el-option>
                        <el-option label="Nutrients (PPM)" value='nutrient_ppm'></el-option>
                        <el-option label="Nutrients (PPM Average)" value='nutrient_ppm_avg'></el-option>
                      </el-select>
                    </base-input>
                  </b-col>
                  <b-col md>
                    <base-input label="Graph Start Date" name="start date" vid="graph_start_date">
                      <flat-picker slot-scope="{focus, blur}"
                        @on-open="focus"
                        @on-close="blur"
                        @on-change="syncFields"
                        placeholder="Graph Start Date"
                        class="form-control datepicker"
                        v-model="model.graph_start_date" :config="flatPickrConfig">
                      </flat-picker>
                    </base-input>
                  </b-col>
                </b-row>
              </validation-observer>
            </div>
          </card>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable no-new */
import { Select, Option } from 'element-ui'
import flatPicker from "vue-flatpickr-component";
import "flatpickr/dist/flatpickr.css";
import swal from 'sweetalert2';
import mab_utils from '../../util/mab-utils';
import NiftyGauge from '../../custom_components/NiftyGauge';

export default {

  mixins: [ mab_utils ],

  components: {
    [Select.name]: Select,
    [Option.name]: Option,
    flatPicker,
    NiftyGauge
  },

  data() {
    return {
      initial: true,
      loading: false,
      date_now: '',
      applied_template: '',

      company_id: null,
      field_id: '',
      field_name: '',

      browseTemplatesModalVisible: false,
      manageTemplateModalVisible: false,

      templateTableColumns: [
        {
          key: 'name',
          label: 'Template',
          sortable: true,
          tdClass: 'valign'
        },
        {
          key: 'created_at',
          label: 'Date Created',
          sortable: true,
          tdClass: 'valign'
        },
        {
          key: 'user_name',
          label: 'Created By',
          sortable: true,
          tdClass: 'valign'
        },
        {
          key: 'actions',
          label: 'Actions'
        }
      ],

      currentMode: 'Add',

      loadedTemplates: [],
      loadedTemplateId: null,
      loadedTemplateName: 'Default Template',
      latestDateReported: '1970-01-01 00:00:00',

      totalRows: 1,
      currentPage: 1,
      perPage: 10,
      filterText: '',

      gauge_value: 0,

      raw: {
        M3_1: 0,
        M4_1: 0,
        M5_1: 0,
        M6_1: 0,
        avg: 0
      },

      ppm: {
        M3_1: 0,
        M4_1: 0,
        M5_1: 0,
        M6_1: 0,
        avg: 0
      },

      pounds: {
        M3_1: 0,
        M4_1: 0,
        M5_1: 0,
        M6_1: 0,
        avg: 0
      },

      model: {
        node_id: null,
        node_address: null,
        install_depth: 2.4,
        graph_type: 'nutrients',
        graph_start_date: '',
      },

      template_name: null,
      template_model: {
        id: null,
        poly1: 0,
        poly2: 0,
        lower_limit: 0,
        upper_limit: 0,
        metric: '',
        soil_type: '',
        crop_name: ''
      },

      lower_limit: 0,
      upper_limit: 0,
      nutrient_label: '',

      soilTypes: [
        { value: 1,  label: 'Sand' },
        { value: 2,  label: 'Loamy Sand' },
        { value: 3,  label: 'Sandy Loam' },
        { value: 4,  label: 'Loam' },
        { value: 5,  label: 'Silt Loam' },
        { value: 6,  label: 'Silt' },
        { value: 7,  label: 'Sandy Clay Loam' },
        { value: 8,  label: 'Clay Loam' },
        { value: 9,  label: 'Silty Clay Loam' },
        { value: 10, label: 'Sandy Clay' },
        { value: 11, label: 'Silty Clay' },
        { value: 12, label: 'Clay' }
      ],

      metrics: [
        { value: 'ppm',    label: 'Parts per million (PPM)' },
        { value: 'pounds', label: 'Pounds' }
      ],

      install_depths: [
        { value: 1.8, label: '4" - 1.8 Pound Factor' },
        { value: 2.1, label: '6" - 2.1 Pound Factor' },
        { value: 2.4, label: '8" - 2.4 Pound Factor' }
      ]

    }
  },

  computed: {
    flatPickrConfig: function() {
      return { maxDate: this.date_now }
    },
  },

  methods: {

    loadNutrients()
    {
      this.gauge_value = 0;
      this.loading = true;
      this.$axios.get(this.$store.state.baseUrl+"/api/ManageNutrients/" + this.$route.params.node_address)
      .then((resp) => {
        this.loading = false;
        if(resp.data){

          this.company_id = resp.data.company_id;
          this.field_id = resp.data.field_id;
          this.field_name = resp.data.field_name;

          this.lower_limit = resp.data.lower_limit;
          this.upper_limit = resp.data.upper_limit;
          this.nutrient_label = resp.data.nutrient_label;

          this.metric = resp.data.metric;
          this.gauge_value = resp.data.gauge_value;

          this.raw = resp.data.raw;
          this.ppm = resp.data.ppm;
          this.pounds = resp.data.pounds;

          this.loadedTemplateId = resp.data.active_template_id;
          this.loadedTemplateName = resp.data.active_template_name;
          this.latestDateReported = resp.data.latest_date_reported;

          if(this.initial){
            this.model.node_id = resp.data.node_id;
            this.model.node_address = resp.data.node_address;
            this.model.install_depth = resp.data.install_depth;
            this.model.graph_type = resp.data.graph_type;
            this.model.graph_start_date = resp.data.graph_start_date;
          }

          this.date_now = resp.data.date_now;

          // nasty fix for a nasty datepicker
          setTimeout(() => { this.initial = false; }, 1000);
        }
      });
    },

    /* Load Saved Cultivar Templates */
    loadNutrientTemplates()
    {
      this.loading = true;
      this.$axios.get(this.$store.state.baseUrl+"/api/loadNutrientTemplates/" + this.company_id)
      .then((resp) => {
        this.loading = false;
        this.loadedTemplates = resp.data.templates;
        this.totalRows = this.loadedTemplates.length;
      });
    },

    syncFields()
    {
      if(this.initial) return;

      this.$refs.nform.validate()
      .then(success => {

        if (!success) { 
          this.$notify({
            title: 'Required',
            message: 'Please fill in all fields',
            type: 'danger',
            verticalAlign: 'top',
            horizontalAlign: 'right',
            duration:1000,
            clean:true
          });
          return;
        }

        this.loading = true;
        this.$axios.post(this.$store.state.baseUrl+"/api/SaveNutrients", { model: this.model })
        .then((resp) => {
          this.loading = false;
          if(resp.data.message == 'field_updated'){
            this.loadNutrients();
            this.$notify({
              title: 'Saved',
              message: 'Changes were saved',
              type: 'success',
              verticalAlign: 'top',
              horizontalAlign: 'right'
            });
            this.$refs.nform.reset();
          }
        });

      });
    },

    addNutrientTemplate()
    {
      this.currentMode = 'Add';
      this.clearNutrientTemplateModel();
      this.openManageNutrientTemplateModal();
    },

    editNutrientTemplate(index, item)
    {
      this.currentMode = 'Edit';
      let model = JSON.parse(item.template);
      this.template_name = item.name;
      this.template_model = Object.assign({}, model);
      this.template_model.id = item.id;
      this.openManageNutrientTemplateModal();
    },

    removeNutrientTemplate(index, item)
    {
      swal.fire({
        title: 'Remove Template?',
        text: 'Template will be permanently deleted',
        showCancelButton: true,
        confirmButtonText: 'Remove Template',
        buttonsStyling: false,
        customClass: {
          cancelButton: 'btn btn-outline-primary',
          confirmButton: 'btn btn-primary'
        }
      }).then(result => {
        if (result.value) {
          this.loading = true;
          this.$axios.post(this.$store.state.baseUrl+"/api/removeNutrientTemplate", {id: item.id})
          .then((resp) => {
            this.loading = false;
            if(resp.data.status == 'template_removed'){

              this.loadNutrients();

              /* Update table to reflect deletion */
              let indexToDelete = this.loadedTemplates.findIndex(row => row.id === item.id);
              if (indexToDelete >= 0) { this.loadedTemplates.splice(indexToDelete, 1); }

              this.$notify({
                title: 'Success',
                message: 'Template Removed',
                type: 'success',
                verticalAlign: 'top',
                horizontalAlign: 'right'
              });

            }
          });
        }
      });
    },

    saveNutrientTemplate()
    {
      this.$refs.managetpl.validate().then(success => {
        if (!success) { return; }

        this.loading = true;
        this.$axios.post(this.$store.state.baseUrl+"/api/saveNutrientTemplate", { 
          name: this.template_name,
          template: JSON.stringify(this.template_model),
          new: this.template_model.id === null ? 'yes' : 'no',
          company_id: this.company_id,
          id: this.template_model.id
        }).then((resp) => {
          this.loading = false;
          if(resp.data.status == 'template_saved'){
            
            // REFRESH TEMPLATES
            this.loadNutrients();
            this.loadNutrientTemplates();
            this.$notify({
              title: 'Success',
              message: 'Template ' + (this.currentMode == 'Add' ? 'Added' : 'Updated'),
              type: 'success',
              verticalAlign: 'top',
              horizontalAlign: 'right'
            });
            this.closeManageNutrientTemplateModal();

          } else if(resp.data.status == 'template_save_error'){
            this.$notify({
              title: 'Failed',
              message: 'Failed to save template. Contact your admin.',
              type: 'warning',
              verticalAlign: 'top',
              horizontalAlign: 'right'
            });
          }
        });
      });
    },

    applyNutrientTemplate(index, item)
    {
      swal.fire({
        title: 'Apply Template?',
        text: 'Template will be applied to the current nutrient configuration',
        showCancelButton: true,
        confirmButtonText: 'Apply Template',
        buttonsStyling: false,
        customClass: {
          cancelButton: 'btn btn-outline-primary',
          confirmButton: 'btn btn-primary'
        }
      }).then(result => {
        if (result.value) {
          this.loading = true;
          this.$axios.post(this.$store.state.baseUrl+"/api/applyNutrientTemplate", {
            id: item.id,
            node_address: this.model.node_address
          }).then((resp) => {
            this.loading = false;
            this.loadedTemplateId = item.id;
            this.loadedTemplateName = item.name;
            if(resp.data.status == 'template_applied'){
              this.closeBrowseTemplatesModal();
              this.loadNutrients();
              this.$notify({
                title: 'Success',
                message: 'Template applied',
                type: 'success',
                verticalAlign: 'top',
                horizontalAlign: 'right'
              });
            }
          });
        }
      });
    },

    clearNutrientTemplateModel()
    {
      this.currentMode = 'Add';
      this.template_model.id = null;
      this.template_model.poly1 = 0;
      this.template_model.poly2 = 0;
      this.template_model.lower_limit = 0;
      this.template_model.upper_limit = 0;
      this.template_model.metric = '';
      this.template_model.soil_type = '';
      this.template_model.crop_name = '';
      if(typeof this.$refs['managetpl'] !== 'undefined'){
        this.$refs['managetpl'].reset();
      }
    },

    openNutrientTemplatesModal()
    {
      this.loadNutrientTemplates();
      this.browseTemplatesModalVisible = true;
    },

    closeBrowseTemplatesModal()
    {
      this.browseTemplatesModalVisible = false;
    },

    openManageNutrientTemplateModal()
    {
      this.manageTemplateModalVisible = true;
    },

    closeManageNutrientTemplateModal()
    {
      this.clearNutrientTemplateModel();
      this.manageTemplateModalVisible = false;
    },

    goToGraph()
    {
      this.$router.push({
        name: "nutrients_graph",
        params:{
          node_id: this.model.node_id,
          node_address: this.model.node_address,
          field_id: this.field_id,
          field_name: this.field_name,
          graph_type: this.model.graph_type,
          graph_start_date: this.model.graph_start_date
        }
      });
    }
  },

  created()
  {
    this.loadNutrients();
  }
};
</script>
